import VKConnect from "@vkontakte/vk-connect";

import {store} from "../../index";

import {setColorScheme, setAccessToken} from "../store/vk/actions";

const APP_ID = 7346020;
const API_VERSION = '5.92';

export const initApp = () => (dispatch) => {
    const VKConnectCallback = (e) => {
        if (e.detail.type === 'VKWebAppUpdateConfig') {
            VKConnect.unsubscribe(VKConnectCallback);

            dispatch(setColorScheme(e.detail.data.scheme));
        }
    };

    VKConnect.subscribe(VKConnectCallback);
    return VKConnect.send('VKWebAppInit', {}).then(data => {
        return data;
    }).catch(error => {
        return error;
    });
};

export const getAuthToken = (scope) => (dispatch) => {
    VKConnect.send("VKWebAppGetAuthToken", {
        "app_id": APP_ID,
        "scope": scope.join(',')
    }).then(data => {
        dispatch(setAccessToken(data.access_token));
    }).catch(() => {
        dispatch(setAccessToken(null));
    });
};

export const closeApp = () => {
    return VKConnect.send("VKWebAppClose", {
        "status": "success"
    }).then(data => {
        return data;
    }).catch(error => {
        return error;
    });
};

export const swipeBackOn = () => {
    return VKConnect.send("VKWebAppEnableSwipeBack", {}).then(data => {
        return data;
    }).catch(error => {
        return error;
    });
};

export const swipeBackOff = () => {
    return VKConnect.send("VKWebAppDisableSwipeBack", {}).then(data => {
        return data;
    }).catch(error => {
        return error;
    });
};

export const groupsGet = () => {
    return APICall('groups.get', {
        "extended": "1",
        "fields": "description",
        "count": "100"
    });
};

CanvasRenderingContext2D.prototype.roundRect = function (x, y, width, height, radius) {
  if (width < 2 * radius) radius = width / 2;
  if (height < 2 * radius) radius = height / 2;
  this.beginPath();
  this.moveTo(x + radius, y);
  this.arcTo(x + width, y, x + width, y + height, radius);
  this.arcTo(x + width, y + height, x, y + height, radius);
  this.arcTo(x, y + height, x, y, radius);
  this.arcTo(x, y, x + width, y, radius);
  this.closePath();
  return this;
}


export const testStory = () => {

var canvas = document.createElement('canvas');
canvas.width = document.body.clientHeight;
canvas.height = document.body.clientWidth;
			var ctx = canvas.getContext("2d");
			
			var posX = 0;
			var posY = 0;
			var text = "В чём смысол жизни?"
			ctx.font = "90px Tahoma";

			var textWidth = ctx.measureText(text).width;
			var textHeight = parseInt(ctx.font);

			ctx.roundRect(posX+10, posY, canvas.width-20, textHeight+textHeight/2, 15);
			ctx.fillStyle = 'rgba(255, 217, 101, 1)';
			ctx.fill();

			ctx.fillStyle = 'rgba(0, 0, 0, 1)';
			ctx.fillText(text, textWidth/4, textHeight);
canvas.toDataURL()

		
		
            return VKConnect.send("VKWebAppShowStoryBox", {
                "background_type": "none",
                "stickers": [
                    {
                    "sticker_type": "renderable",
                    "sticker": {
                        "can_delete": true,
                        "content_type": "image",
                        "blob": canvas.toDataURL(),
						"original_width": 200,
						"original_height": 30
                    }
                    }
                ]
              }).then(data => {
        return data.response;
    }).catch(error => {
        return error;
    });
}

export const APICall = (method, params) => {
    params['access_token'] = store.getState().vkui.accessToken;
    params['v'] = params['v'] === undefined ? API_VERSION : params['v'];

    return VKConnect.send("VKWebAppCallAPIMethod", {
        "method": method,
        "params": params
    }).then(data => {
        return data.response;
    }).catch(error => {
        return error;
    });
};
